import React from 'react';
import { getLastChildrenOfReactNode } from '../../../utils/getLastChildOfReactNode';
import { StyledAnchorH2 } from './styled';

const getAnchor = (text: string): string => {
  return text
    .toLowerCase()
    .replace(/[^a-z0-9 ]/g, '')
    .replace(/[ ]/g, '-');
};

const AnchorH2 = (props : { children: React.ReactElement }) => {
  const anchorText = getLastChildrenOfReactNode(props.children);

  const anchor = getAnchor(anchorText);
  const link = `#${anchor}`;

  return (
    <StyledAnchorH2 id={anchor}>
      <a href={link}>
      {props.children}
      </a>
    </StyledAnchorH2>
  );
};

export default AnchorH2;
